import React from "react";
import "./style.less";
export default function MvInformation(props) {
  return (
    <div className="mv-information">
      <span>{props.data.name}</span>
      <div className="information-text">
        <div>:{props.data.artistName}</div>
        <div>:{props.data.playCount}</div>
        <div>:{props.data.publishTime}</div>
      </div>
      <br />
      <div className="mv-jieshao">
        :
        {props.data.desc !== null && props.data.desc !== ""
          ? props.data.desc
          : "!"}
      </div>
    </div>
  );
}
