import { SEARCH_HISTORY, INIT_CLEAR } from "../constant"

const initState = ["", "", "", ""]

export default function Search(preState = initState, action) {
    const { type, data } = action
    switch (type) {
        case SEARCH_HISTORY:
            return [data, ...preState]
        case INIT_CLEAR:
            return []
        default:
            return preState
    }
}