import { createdsidebar } from "../../redux/actions/sidebar";
import { createduser } from "../../redux/actions/user";
import { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createduserdetails } from "../../redux/actions/user_detail";
import { useHistory } from "react-router-dom";
import api from "../../api";
import "./style.less";

export default function LoginMessage() {
  let history = useHistory();
  const sidebar = useSelector((state) => state.sidebar);
  const [flag, setflag] = useState(false);
  const [flag2, setflag2] = useState(false);
  var timer1 = null;
  var timer2 = null;
  const dispatch = useDispatch();
  function guanbi() {
    dispatch(createdsidebar(false));
  }
  function loginout() {
    if (localStorage.getItem("user") === null) {
      setflag(true);
      timer1 = setTimeout(() => {
        setflag(false);
      }, 2000);
    } else {
      setflag2(true);
      timer2 = setTimeout(() => {
        setflag2(false);
      }, 2000);
      localStorage.removeItem("user");
      localStorage.removeItem("cookie");
      api.getlogout();
      //
      dispatch(createduser(null));
      //
      dispatch(createdsidebar(false));
      //
      dispatch(createduserdetails(null));
      history.push("/");
    }
  }

  useEffect(() => {
    return () => {
      clearTimeout(timer1);
      clearTimeout(timer2);
    };
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  return (
    <div>
      {sidebar ? (
        <div className="login-message">
          <div className="left">
            <div className="vip">
              <h1>VIP</h1>
              <p>0.3/</p>
            </div>
            <div className="xinxi">
              <ul>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <div className="fuwu">
              <p></p>
              <ul>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <div className="qita">
              <p></p>
              <ul>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <div className="dingdan">
              <p></p>
              <ul>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
              </ul>
            </div>
            <div className="tuichulogin" onClick={loginout}>
              /
            </div>
          </div>
          <div className="right" onClick={guanbi}></div>
          {flag ? (
            <div className="message-tanchuang">,</div>
          ) : null}
          {flag2 ? <div className="message-tanchuang"></div> : null}
        </div>
      ) : null}
    </div>
  );
}
