import React from "react";
import { useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { useState, useEffect } from "react";
// import { Link } from "react-router-dom";
import "./style.less";
export default function SecondHeaderNav() {
  let history = useHistory();
  const userinformation = useSelector((state) => state.user);
  var x = null;
  var y = null;
  useEffect(() => {
    return () => {
      clearTimeout(x);
      clearTimeout(y);
    };
  }, []); // eslint-disable-line react-hooks/exhaustive-deps
  const [flag, setflag] = useState(false);
  const [loginflag, setloginflag] = useState(false);
  function show() {
    setflag(true);
    x = setTimeout(() => {
      setflag(false);
    }, 1000);
  }
  function loginpd() {
    if (userinformation === null) {
      setloginflag(true);
      y = setTimeout(() => {
        setloginflag(false);
      }, 1000);
    } else {
      history.push("/tuijian");
    }
  }
  function goaway() {
    history.push("/paihangbang");
  }
  return (
    <div>
      <div className="scond-header-nav">
        <ul className="clear-fix">
          <li>
            <i className="iconfont icon-rili" onClick={loginpd}></i>
            
          </li>
          <li>
            <i
              className="iconfont icon-luyinjishouyinjidiantai"
              onClick={show}
            ></i>
            FM
          </li>
          <li>
            <i className="iconfont icon-gedan" onClick={show}></i>
            
          </li>
          <li>
            <i
              className="iconfont icon-paihangbang_paiming"
              onClick={goaway}
            ></i>
            
          </li>
          <li>
            <i className="iconfont icon-zhuanjiguangpan" onClick={show}></i>
            
          </li>
        </ul>
        {flag ? <div className="zwkf">,</div> : null}
        {loginflag ? <div className="zwkf"></div> : null}
      </div>
    </div>
  );
}
